import SynargyComponents from "../src/synargy-components";
const template = (self) => {
  return `<div>
  <p s-bind-text="number" id="counter">{{self.$data.number}}</p>
  <p s-bind-text="number" id="counter">55</p>
  <p s-bind-do="number" s-do="greet" id="counter">{{$data.number}}</p>
  <button s-onclick="add" id="add">add</button>
</div>
`;
};
class TestComp extends SynargyComponents {
  constructor() {
    super();
    this.data = {
      number: 0,
      number_2: 0,
    };
  }
  greet() {
    console.log("hello");
  }
  add() {
    console.log(this.$data.number);
    this.$data.number++;
    this.$data.number_2++;
  }
  connectedCallback() {
    this.render(template);
    
  }
}

customElements.define("test-comp", TestComp);
